/*
  ========================================
  Common Spacing
  ========================================
*/

.container {
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
  max-width: 960px;
}

/*
  ========================================
  Header
  ========================================
*/

header {
  width: 100%;
  height: 65px;
}

.main-nav {
  position: fixed;
  color: #82abb0;
  background-color: #021723;
  width: 100%;
}

.main-nav ul {
  text-transform: uppercase;
  width: 100%;
}

.main-nav li {
  text-align: center;
  display: inline-block;
  width: 22.5%;
  padding: 10px 0 5px 0;
}

.main-nav li:first-child {
  width: 10%;
}

.main-nav img {
  height: 50px;
  width: 50px;
	vertical-align: middle;
}

.active {
  border-bottom: 4px solid #ED3F91;
  pointer-events: none;
  cursor: default;
}

/*
  ========================================
  Typography
  ========================================
*/

body {
  color: #888;
  font: 300 16px/26px "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

h1 {
  font-size: 28px;
  text-align: center;
  text-transform: uppercase;
  margin-top: 40px;
  margin-bottom: 40px;
}

h2 {
  font-size: 24px;
  line-height: 44px;
}

h3 {
  font-size: 21px;
  text-align: center;
  text-transform: uppercase;
}

h4 {
  font-size: 18px;
}

h5 {
  color: #a9b2b9;
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 8px;
}

strong {
  font-weight: 400;
}

cite, em {
  font-style: italic;
}

h3, p {
  margin-bottom: 22px;
  margin-top: 22px;
}

/*
  ========================================
  Links
  ========================================
*/

a:hover {
  color: #ed3f91;
}

a {
  color: #009999;
  text-decoration: none;
}

p a {
  border-bottom: 1px solid #009999;
}

/*
  ==============================================
  Footer
  ==============================================
*/

footer {
  color: #82abb0;
  background-color: #021723;
  padding: 12px;
}


footer p {
  float: left;
  margin-top: 20px;
  margin-left: 28px;
}

.social-icons {
  float: right;
  padding-right: 28px;
}

.social-icons a {
  padding: 3px;
  margin: 5px;
  border-radius: 50%;
}

.social-icons a:hover {
  background-color: #009999;
}

.social-icons img {
  border-radius: 50%;
  background-color: #021723;
}

.social-icons a:hover img {
  background-color: #173543;
}

/*
  ========================================
  Clearfix
  ========================================
*/

.group:before,
.group:before,
.group:after {
  content: "";
  display: table;
}
.group:after {
  clear: both;
}
.group {
  clear: both;
  *zoom: 1;
}

/*
  ========================================
  Buttons
  ========================================
*/

.btn {
  border-radius: 5px;
  display: inline-block;
  border: 1px solid #009999;
  padding: 10px 30px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 12px;
}

.btn-alt {
  padding: 8px 20px;
  background-color: #EDEDED;
}

.btn-wrap {
  text-align: center;
}

.btn:hover {
	border-color: #ED3F91;
	background-color: #51f6e8;
}
